import { profileAPI, usersAPI } from "../api/api";

const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE = 'SET-USER-PROFILE';
const SET_STATUS = 'SET-STATUS';
const DELETE = 'DELETE';

let initialState = {
  posts: [
    { id: 1, message: 'Hi, how are you?', likesCount: 12 },
    { id: 2, message: 'Its, how are you?', likesCount: 10 },
    { id: 3, message: 'Привет как дела', likesCount: 12 },
    { id: 4, message: 'позвонишь?', likesCount: 10 },
    { id: 5, message: 'Фашисты работают над документацией', likesCount: 12 },
    { id: 6, message: 'Димыч красавчик', likesCount: 10 },
  ],
  profile: null,
  status: '1',
}

const profileReducer = (state = initialState, action) => {
  let stateCopy;

  switch (action.type) {
    case DELETE:
      return { ...state, posts: state.posts.filter((u) => u.id != action.postId) }
    case ADD_POST:
      return stateCopy = {
        ...state,
        newPostText: '',
        posts: [...state.posts, { id: 3, message: action.newPostText, likesCount: 0 }]
      };
    case SET_USER_PROFILE:
      return stateCopy = {
        ...state,
        profile: action.profile
      };
    case SET_STATUS:
      return stateCopy = {
        ...state,
        status: action.status
      };
    default:
      return state
  }
}

export const addPostActionCreator = (newPostText) => ({ type: 'ADD-POST', newPostText });
export const setUserProfile = (profile) => ({ type: 'SET-USER-PROFILE', profile });
export const setStatus = (status) => ({ type: 'SET-STATUS', status });
export const deletedPosts = (postId) => ({ type: 'DELETE', postId });

export const getUserProfile = (userId) => async (dispatch) => {

  let response = await usersAPI.getProfile(userId);
  dispatch(setUserProfile(response.data));
};

export const getStatus = (userId) => async (dispatch) => {

  let response = await profileAPI.getStatus(userId);
  dispatch(setStatus(response.data));
};

export const updateStatus = (status) => async (dispatch) => {

  let response = await profileAPI.updateStatus(status);
  if (response.data.resultCode === 0) {
    dispatch(setStatus(status));
  }
};

export default profileReducer;
