import React from 'react';
import { connect } from 'react-redux';
import { follow, requestUsers, setCurrentPage, unfollow, toggleFollowingProgress } from "../../redux/users-reducer";
import Users from './Users';
import Preloader from '../common/Preloader/Preloader';
import { Navigate } from 'react-router-dom';
import { compose } from 'redux';
import { getCurrentPage, getFollowingInProgress, getIsAuth, getIsFetching, getPageSize, getTotalUsersCount, getUsers } from '../../redux/users-selectors';

class UsersContainer extends React.Component {

  componentDidMount() {
    let {currentPage, pageSize} = this.props;
    this.props.requestUsers(currentPage, pageSize);
  }; //запрос на сервер  за  users 

  onPageChanged = (pageNumber) => {
    let {pageSize} = this.props;
    this.props.requestUsers(pageNumber, this.props.pageSize);
  }; //Странички

  render() {
    if (this.props.isAuth == false) { return <Navigate to='/login' /> }
    return <>
      {this.props.isFetching ? <Preloader /> : null}
      <Users
        totalUsersCount={this.props.totalUsersCount}
        pageSize={this.props.pageSize}
        currentPage={this.props.currentPage}
        onPageChanged={this.onPageChanged}
        users={this.props.users}
        follow={this.props.follow}
        unfollow={this.props.unfollow}
        followingInProgress={this.props.followingInProgress}
      />
    </>
  }
}

let mapStateToProps = (state) => {
  return {
    users: getUsers(state),
    pageSize: getPageSize(state),
    totalUsersCount: getTotalUsersCount(state),
    currentPage: getCurrentPage(state),
    isFetching: getIsFetching(state),
    followingInProgress: getFollowingInProgress(state),
    isAuth: getIsAuth(state),
  }
}

export default compose(
  connect(mapStateToProps,
    { follow, unfollow, setCurrentPage, toggleFollowingProgress, requestUsers }))(UsersContainer)
