import { stopSubmit } from "redux-form";
import { authAPI } from "../api/api";

const SET_USER_DATE = 'samurai-network/auth/SET-USER-DATE';

let initialState = {
  userId: null,
  email: null,
  login: null,
  isAuth: false,
  autorizedUserId: null,
};

const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_USER_DATE:
      return {
        ...state,
        ...action.payload
      }
    default:
      return state;
  }
};

export const setAuthUserDate = (userId, email, login, isAuth) => ({ type: 'samurai-network/auth/SET-USER-DATE', payload: { userId, email, login, isAuth } });

export const getAuthUserData = () => async (dispatch) => {
  
  let response = await authAPI.me();
  if (response.resultCode === 0) {
    let { id, login, email } = response.data;
    dispatch(setAuthUserDate(id, email, login, true));
  }
}

export const login = (email, password, rememberMe) => async (dispatch) => {

  let response = await authAPI.login(email, password, rememberMe)
  if (response.resultCode === 0) {
    getAuthUserData();
  } else {
    let message = response.messages.length > 0 ? response.messages[0] : 'Common error';
    dispatch(stopSubmit('login', { _error: message }))
  }
}

export const logout = () => async (dispatch) => {

  let responce = await authAPI.logout();
  if (responce.resultCode === 0) {
    dispatch(setAuthUserDate(null, null, null, false));
  }
}

export default authReducer;
